.aside {
  z-index: 22;
  border: 0;
  &-backdrop {
    z-index: 20; }
  &-header {
    background-color: #dddedf;
    border-bottom: 3px solid #B5B9C0;
    border-top: 1px solid #B5B9C0;
    color: #000;
    padding: .8em; }
  &-contents {
    padding: 0;
    .nav-item {
      width: 50%;
      .nav-link {
        text-transform: uppercase;
        font-weight: bold;
        text-align: center; } }
    #contact {
      height: auto;
      overflow-x: hidden;
      .row {
        display: block;
        .caption {
          margin: 10px 0 0;
          color: #466ba1;
          font-size: 18px;
          text-align: center;
          text-decoration: none; }
        .col_1, .col_2 {
          display: inline-block; }
        .col_1 {
          text-align: right;
          max-width: 50%; }
        .col_2 {
          padding: 0;
          text-align: left; }
        p {
          font-size: 12px;
          a[href^="tel:"] {
            text-decoration: none;
            color: #000000; }
          i {
            margin: 0 5px; } } } }
    .feedEkList {
      height: auto;
      overflow-x: hidden; } }
  &.in {
    width: 400px;
    min-width: 320px; } }
